/* CSS Reset */
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  font: inherit;
}

html {
  color-scheme: dark light;
}

img,
picture,
svg,
video {
  display: block;
  max-width: 100%;
}

/* Custom CSS */
:root {
  --primary-color: #38bdf8;
  --secondary-color: #334155;
  --third-color: #1e293b;
  --white-color: #fafafa;
  --gray-color: #94a3b8;
  --primary-color-svg: brightness(0) saturate(100%) invert(60%) sepia(16%)
    saturate(2562%) hue-rotate(169deg) brightness(107%) contrast(95%);
  --white-color-svg: brightness(0) saturate(100%) invert(100%) sepia(0%)
    saturate(0%) hue-rotate(14deg) brightness(105%) contrast(96%);
  --primary-button-color: #475569;
  --primary-button-hover-color: #53657d;
  --secondary-button-color: #38bdf8;
  --secondary-button-hover-color: #7dd3fc;
}
:root.light {
  --primary-color: #38bdf8;
  --secondary-color: #7497c9;
  --third-color: #5372a3;
  --gray-color: #abbfda;
  --primary-button-color: #6c84a4;
}

.not-active {
  display: none !important;
}

.content {
  min-height: 100vh;
  background-color: var(--third-color);
  font-family: "Roboto", sans-serif;
  display: grid;
  grid-template: "sidebar header" 100px "sidebar main" 1fr/300px 1fr;
}

.hide-sidebar {
  grid-template: "header header" 100px "main main" 1fr/300px 1fr !important;
}

h1 {
  font-family: "Montserrat", sans-serif;
}

h1,
h2,
h3,
h4,
p {
  color: var(--white-color);
}

/* Sidebar general */
.sidebar {
  grid-area: sidebar;
  padding: 40px;
  background-color: var(--secondary-color);
  border-right: 1px solid var(--gray-color);
}
.sidebar h2 {
  color: var(--gray-color);
  margin-top: 30px;
  margin-bottom: 20px;
}

/* Sidebar top */
.sidebar-top {
  margin-bottom: 100px;
}
.sidebar-top img {
  filter: var(--primary-color-svg);
  width: 22px;
}
.sidebar-top .banner {
  display: flex;
  align-items: center;
  gap: 10px;
}
.sidebar-top .banner h1 {
  font-size: 2.1em;
}
.sidebar-top .banner img {
  width: 34px;
}
.sidebar-top ul {
  list-style: none;
}
.sidebar-top ul li {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
  cursor: pointer;
  transition: transform 0.3s;
}
.sidebar-top ul li:hover {
  transform: translate(5px);
}
.sidebar-top ul li p {
  font-size: 1.1em;
}
.sidebar-top .create-project {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: 10px 20px;
  margin-left: 10px;
  border-radius: 10px;
  transition: background-color 0.3s;
}
.sidebar-top .create-project img {
  width: 18px;
  border: 2px solid var(--primary-color);
  border-radius: 100%;
}
.sidebar-top .create-project p {
  font-size: 0.8em;
}
.sidebar-top .create-project:hover {
  background-color: rgba(0, 0, 0, 0.3);
}

/* Sidebar bottom */
.sidebar-bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 30px;
}
.sidebar-bottom img {
  filter: var(--white-color-svg);
}
.sidebar-bottom .light-dark-mode {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
}
.sidebar-bottom .light-dark-mode img {
  width: 25px;
}
.sidebar-bottom .collapse {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 10px 20px;
  background-color: var(--primary-button-color);
  border-radius: 10px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s;
}
.sidebar-bottom .collapse img {
  width: 20px;
}
.sidebar-bottom .collapse p {
  font-size: 0.9em;
}
.sidebar-bottom .collapse:hover {
  background-color: var(--primary-button-hover-color);
}
.sidebar-bottom footer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.sidebar-bottom footer a {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  text-decoration: none;
  color: var(--white-color);
}
.sidebar-bottom footer img {
  width: 20px;
}

/* light and dark mode switch */
/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 42px;
  height: 24.33px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--primary-color);
  border-radius: 23.3px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 16.66px;
  width: 16.66px;
  border-radius: 50%;
  left: 20.66px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: var(--gray-color);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--gray-color);
}

input:checked + .slider:before {
  -webkit-transform: translateX(-16.66px);
  -ms-transform: translateX(-16.66px);
  transform: translateX(-16.66px);
}

header {
  grid-area: header;
  background-color: var(--secondary-color);
  border-bottom: 1px solid var(--gray-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 40px;
}
header h2 {
  font-size: 1.8em;
}
header .hamburger-menu {
  background-color: var(--primary-button-color);
  border-radius: 100%;
  padding: 10px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s;
}
header .hamburger-menu:hover {
  background-color: var(--primary-button-hover-color);
}
header img {
  width: 25px;
  filter: var(--white-color-svg);
}

.header-right {
  display: flex;
  gap: 20px;
}
.header-right .add-task-button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--secondary-button-color);
  padding: 8px 16px;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  transition: background-color 0.2s;
}
.header-right .add-task-button:hover {
  background-color: var(--secondary-button-hover-color);
}
.header-right .edit-project-button {
  background-color: var(--primary-button-color);
  border-radius: 100%;
  padding: 10px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s;
}
.header-right .edit-project-button:hover {
  background-color: var(--primary-button-hover-color);
}

label {
  color: var(--white-color) !important;
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  z-index: 10;
  width: 400px;
  padding: 20px;
  border-radius: 10px;
  background-color: var(--secondary-color);
}

.active {
  transform: translate(-50%, -50%) scale(1);
}

.popup-header {
  margin-bottom: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.popup-header h2 {
  font-size: 1.2em;
  font-weight: bold;
}
.popup-header button {
  background-color: var(--primary-button-color) !important;
  padding: 5px;
  border: none;
  cursor: pointer;
  border-radius: 100%;
  transition: background-color 0.3s;
}
.popup-header button img {
  width: 15px;
  filter: var(--white-color-svg);
}
.popup-header button:hover {
  background-color: var(--primary-button-hover-color) !important;
}

form {
  display: grid;
  grid-template-columns: 1fr;
  padding: 0px 6px;
}
form label {
  margin-bottom: 8px;
  font-size: 0.9em;
}
form input,
form textarea,
form select {
  padding: 10px;
  border-radius: 5px;
  border: 1px solid var(--gray-color);
  margin-bottom: 20px;
  font-size: 0.9em;
}

input:focus {
  outline: none;
  padding: 9px;
  cursor: pointer;
  border: 2px solid var(--primary-color);
}

input[type=submit] {
  padding: 10px;
  font-size: 1.1em;
  font-weight: bold;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  background-color: var(--secondary-button-color);
  color: var(--white-color);
  transition: background-color 0.3s;
}

input[type=submit]:hover {
  background-color: var(--secondary-button-hover-color);
}

.overlay {
  position: fixed;
  display: none;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  width: 10000px;
  height: 10000px;
  background-color: rgba(0, 0, 0, 0.5);
  pointer-events: none;
  z-index: 9;
}

.overlay.active {
  display: block;
  pointer-events: all;
}

#opened-task {
  width: 380px !important;
  padding: 40px 30px;
}
#opened-task img {
  width: 20px;
}
#opened-task #opened-task-status {
  color: var(--gray-color);
  font-size: 0.8em;
}
#opened-task #opened-task-title {
  font-size: 1.4em;
}
#opened-task #opened-task-description {
  color: var(--gray-color);
  margin-top: 15px;
  margin-bottom: 20px;
}
#opened-task #opened-task-due {
  width: 120px;
  background-color: var(--primary-button-color);
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.9em;
  margin-bottom: 10px;
}
#opened-task #opened-task-priority {
  width: 120px;
  display: flex;
  gap: 4px;
  font-size: 0.9em;
  background-color: var(--primary-button-color);
  padding: 6px 12px;
  border-radius: 20px;
  margin-bottom: 30px;
}
#opened-task #opened-task-priority img {
  width: 14px;
}
#opened-task #opened-task-priority .low {
  filter: var(--white-color-svg);
}
#opened-task #opened-task-priority .med {
  filter: brightness(0) saturate(100%) invert(55%) sepia(96%) saturate(2343%) hue-rotate(348deg) brightness(97%) contrast(102%);
}
#opened-task #opened-task-priority .high {
  filter: brightness(0) saturate(100%) invert(32%) sepia(83%) saturate(792%) hue-rotate(320deg) brightness(107%) contrast(109%);
}
#opened-task #opened-task-footer {
  display: flex;
  gap: 10px;
}
#opened-task #opened-task-footer > * {
  border: none;
  background-color: var(--secondary-button-color);
  cursor: pointer;
  transition: background-color 0.3s;
}
#opened-task #opened-task-footer > *:hover {
  background-color: var(--secondary-button-hover-color);
}
#opened-task #opened-task-footer #opened-task-main {
  border-radius: 24px;
  padding: 0px 40px;
  font-size: 1.2em;
}
#opened-task #opened-task-footer #opened-task-edit,
#opened-task #opened-task-footer #opened-task-close {
  padding: 8px;
  border-radius: 50%;
  margin-right: 10px;
}
#opened-task #opened-task-footer #opened-task-edit img,
#opened-task #opened-task-footer #opened-task-close img {
  filter: var(--white-color-svg);
  width: 24px;
}

main {
  grid-area: main;
  padding: 40px 80px;
}

#task-display .project-render-task {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}
#task-display h3 {
  margin-bottom: 20px;
  font-weight: bold;
  color: var(--gray-color);
}
#task-display ul {
  display: flex;
  flex-direction: column;
  gap: 30px;
  list-style: none;
}
#task-display ul h4 {
  font-size: 1.2em;
}
#task-display ul li {
  background-color: var(--secondary-color);
  padding: 15px 20px;
  width: 280px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  transition: transform 0.3s, box-shadow 0.3s;
}
#task-display ul li:hover {
  transform: translateY(-3px);
  box-shadow: rgba(0, 0, 0, 0.36) 0px 6px 16px;
}
#task-display ul li .task-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 0.8em;
}
#task-display ul li .task-footer .task-priority {
  display: flex;
  gap: 4px;
  background-color: var(--primary-button-color);
  padding: 4px 8px;
  border-radius: 20px;
}
#task-display ul li .task-footer .task-priority img {
  width: 14px;
}
#task-display ul li .task-footer .task-due-date {
  color: var(--gray-color);
}
#task-display ul li .task-footer .low {
  filter: var(--white-color-svg);
}
#task-display ul li .task-footer .med {
  filter: brightness(0) saturate(100%) invert(55%) sepia(96%) saturate(2343%) hue-rotate(348deg) brightness(97%) contrast(102%);
}
#task-display ul li .task-footer .high {
  filter: brightness(0) saturate(100%) invert(32%) sepia(83%) saturate(792%) hue-rotate(320deg) brightness(107%) contrast(109%);
}

#todo h3::before {
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background-color: var(--primary-color);
  display: inline-block;
  margin-right: 5px;
}

#done h3::before {
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background-color: #6ee7b7;
  display: inline-block;
  margin-right: 5px;
}

.date-container {
  padding: 10px;
  margin-bottom: 100px;
}

.date-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 50px;
  margin-bottom: 40px;
}
.date-header h3 {
  font-size: 1.3em;
  white-space: nowrap;
  height: 5px;
}
.date-header button {
  padding: 7px 17px;
  border-radius: 17px;
  border: none;
  background-color: var(--secondary-button-color);
  font-size: 0.8em;
  cursor: pointer;
  transition: background-color 0.3s;
}
.date-header button:hover {
  background-color: var(--secondary-button-hover-color);
}

.hr {
  height: 1px;
  background-color: var(--gray-color);
  flex: auto;
}

.date-tasks {
  padding: 0px 30px;
  display: grid !important;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)) !important;
  gap: 20px;
}
.date-tasks li {
  width: 100% !important;
}

@media (max-width: 800px) {
  header {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center;
  }
  .header-right .add-task-button p {
    display: none;
  }
  .date-header {
    gap: 20px;
    justify-content: center;
  }
  .date-header .hr {
    display: none;
  }
  .date-header h3 {
    font-size: 1em;
  }
  .date-header button {
    font-size: 0.6em;
  }
  .date-tasks {
    width: 100px;
  }
  .date-tasks li {
    width: 60% !important;
  }
}
@media (max-width: 500px) {
  #task-display li {
    width: 250px !important;
  }
